.banner-img {
  margin-top: 60px;
}

.home-page h1 {
  margin-top: 40px;
  color: black;
  font-family: "Playfair Display", serif;
  font-weight: 700;
}

.home-page h1 span {
  background: #0000e2;
  color: #fff;
  display: inline-block;
  padding: 0.5rem 3rem;
  clip-path: polygon(100% 0, 93% 50%, 100% 99%, 0% 100%, 7% 50%, 0% 0%);
}

.home-page {
  display: flex;
  flex-wrap: wrap;
}

.home-page .card {
  background-color: rgba(128, 128, 128, 0.097);
  width: 18rem;
}

.home-page .card-name-price {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.home-page .card-price {
  color: green;
  font-weight: bold;
}

.home-page button {
  border-radius: 0;
  width: 100%;
  border-top-left-radius: 5px;
  border-bottom-right-radius: 5px;
  font-size: 14px;
  font-weight: bold;
}

.home-page .card-text {
  color: rgb(90, 89, 89);
}

.home-page .card img:hover {
  transform: scale(0.9);
}

.home-page .card-img-top {
  height: 300px !important;
}

/* ==================
filters
============== */
.filters h4 {
  margin-top: 100px;
  color: gray !important;
  border-bottom: 1px solid rgba(128, 128, 128, 0.337);
}

.ant-checkbox-wrapper:first-of-type {
  margin-left: 0px;
}

.ant-checkbox-wrapper {
  margin: 2px;
}

.ant-radio-wrapper {
  margin-bottom: 5px !important;
  margin-left: 10px;
}

.filters button {
  background-color: black;
  width: 100%;
  border: none;
  border-radius: 0;
  margin-top: 20px;
}

.loadmore {
  color: blue;
  font-weight: bold;
  font-size: 20px !important;
}

/* .carousel_link ul {
  display: none;
  height: 410px;
}

*/
.btn-info {
  background-color: blue;
  color: #fff;
  border-color: #0000e2;
}

.btn-info:hover {
  background-color: #00226e;
  color: #fff;
  border-color: #00226e;
}

.btn-dark {
  background-color: #000000;
  color: #fff;
  border-color: #000000;
}

.slider_img img {
  height: 25rem;
}

@media only screen and (max-width: 500px) {

  .slider_img img {
    height: 200px;
  }

  .filters h4 {
    margin-top: 37px;
  }

  .ant-checkbox-wrapper:first-of-type {
    margin-left: 0px;

  }

  .ant-checkbox-wrapper {
    margin: 2px;
    text-align: center;
  }

  .ant-radio-wrapper {
    margin-bottom: 5px !important;
    margin-left: 10px;
    text-align: center;
  }

  .home-page .card {
    background-color: rgba(128, 128, 128, 0.097);
    width: 29rem;
  }
}