.search_baar {
  margin-right: 100px;
}

.search_baar input {
  width: 30rem;
  border: 2px solid#00226e;
  font-size: 18px;
  border-radius: 40px;
  outline: none;
  padding-left: 16px;
}

.search_baar input:hover {
  border: 2px solid#00226e;
  outline: none;

}

.search_baar button {
  background: #0000e2;
  border-radius: 0 39px 39px 0px;
  color: #fff;
  outline: none;
  border: 2px solid#00226e;
  margin-left: -50px;
  width: 69px;
  font-size: 18px;
}

.search_baar button:hover {
  background-color: #00226e;
  border: 2px solid#00226e;

  outline: none;
}

@media only screen and (max-width: 500px) {
  .search_baar {
    margin-right: 0px;
  }

  .search_baar input {
    width: 21rem;
  }
}