@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* font-family: 'Poppins', sans-serif; */

.navbar{
   font-family: 'Poppins', sans-serif; 
   font-size: 17px;
   line-height: 26px;
   text-transform: uppercase;
   box-shadow: 0 -5px 21px gray;
   --webkit-box-shadow:0 -5px 21px gray;
   border-bottom: 0px solid gray !important;

}
.nav-link{
   font-weight: 300 !important;
}
.active{
  border-bottom: 2px solid#000;
}

.navbar-brand{
  font-weight: 700;
  font-family: 'roboto', sans-serif;
  letter-spacing: 3px;
}


/* Footer */
.footer{
  color: #fff;
  padding: 25px;
  font-family: 'roboto' sans-serif;
  background: #000;
  background: linear-gradient(to right,#343434,#000000);
  background: -webkit-linear-gradient(to right,#343434,#000000);
}

.footer a {
text-decoration: none;
color: #fff;
padding: 10px;
}
.footer a:hover {
  color: #ffefba;
  border-bottom: 1px solid #ffefba;
}

/* page not found css */
.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title {
  font-size: 100px;
  font-weight: 700;
}

.pnf-heading {
  font-weight: normal;
}

.pnf-btn {
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}
.pnf-btn:hover {
  background-color: black;
  color: white;
}

/* Contant */
.contactus {
  margin: 0;
  padding: 0;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}


/* Register */
.register{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 70vh;
  flex-direction: column;
}


.product-link{
  text-decoration: none !important;
  color: #000000 !important;
}